.ng2-c-menu {
  display: block; }

.ng2-c-menu__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.ng2-c-menu__item {
  border-bottom: 1px solid #f5f5f5;
  cursor: pointer;
  font-size: 18px;
  font-weight: 400; }

.ng2-c-menu__link {
  color: #28648a;
  cursor: pointer;
  display: block;
  padding: 15px 20px;
  text-decoration: none; }
  @media (min-width: 768px) {
    .ng2-c-view__content .ng2-c-menu__item--is-active .ng2-c-menu__link {
      background: #f5f5f5; } }

.ng2-c-menu__link--with-icon {
  padding-left: 45px;
  position: relative; }
  .ng2-c-menu__link--with-icon [class^="icon-"],
  .ng2-c-menu__link--with-icon [class*=" icon-"] {
    font-size: 20px;
    left: 10px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }
